@import "flex";

body,html {
  height: 100%;
  -webkit-tap-highlight-color: transparent
}

.container {
  overflow: hidden
}

.container,.page {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0
}

.page {
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

.test{
  display: flex;
}
